import Link from 'next/link';
import SpotifyWebApi from 'spotify-web-api-node';

const scopes = [
	'ugc-image-upload',
	'user-read-playback-state',
	'user-modify-playback-state',
	'user-read-currently-playing',
	'streaming',
	'app-remote-control',
	'user-read-email',
	'user-read-private',
	'playlist-read-collaborative',
	'playlist-modify-public',
	'playlist-read-private',
	'playlist-modify-private',
	'user-library-modify',
	'user-library-read',
	'user-top-read',
	'user-read-playback-position',
	'user-read-recently-played',
	'user-follow-read',
	'user-follow-modify',
];

export default async function MyPlaylists() {
	const spotifyApi = new SpotifyWebApi({
		clientId: '0916ebedc8fe494699b4252738eb2444',
		// clientSecret: 'bf470b8b094245afb8799dcfa33f2645',
		redirectUri: 'http://localhost:3000/my-playlists',
	});

	spotifyApi.setAccessToken('');
	const authorizeUrl = spotifyApi.createAuthorizeURL(scopes, 'state');

	return <Link href={authorizeUrl}>Login with Spotify</Link>;
}
